body{
    background-color: var(--white);
    font-family: var(--font);
    height: 520px;
    width: 700px;
}
body {
    --black: #3a3a3c;
    --white: #f0f1f2;
    --grey: #aeaeb2;
    --purple: #b462ff;
    --fuschia: #ff45b4;
    --blue: #18b6ff;
    --green: #1beb9e;
    --orange: #ff9528;
    --yellow: #dddd25;
    --cian: #40c8e0;
    --red: #ff375f;
    --line: 2.5ch;
    --font: Arial, Helvetica, sans-serif;
}

#title{
    display: flex;
    flex-direction: row;
    margin-bottom: 30px;
}

#action-menu {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.action-button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border: none;
    border-radius: 5px;
    background-color: var(--grey);
    color: var(--white);
    font-size: 20px;
}



#taskStatusSection{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    flex-direction: row;

    background-color: var(--white);


    border-radius: 10px;
    padding: 5px;
}

.task-status-title {
    display: flex; /* Permite alinear el contenido horizontalmente */
    justify-content: center; /* Centra horizontalmente el contenido */
    align-items: center; /* Centra verticalmente el contenido */
    width: auto;
    border-radius: 25px;
    margin: 10px 20px;

    padding-top: 0px;
    padding-bottom: 0%;


    
    font-weight: bolder;
    font-size: 1vw; /* Tama√±o de la fuente en unidades de viewport width (vw) */
}

#not-started-title{
    background-color: var(--grey);
    color: #ffffff;
}

#in-progress-title{
    background-color: var(--blue);
    color: #ffffff;
}


#done-title{
    background-color: var(--cian);
    color: #ffffff;
}

.listSpace {
    background-color: #ffffff;
    border-radius: 5px;
    padding: 5px;
    margin: 15px;
    flex-grow: 1;
    max-width: calc(33.33% - 20px);
    box-sizing: border-box;
    height: calc(90vh - 50px);
}





